<template>
  <div>
    <v-data-table v-if="showType === 0" :headers="headers" :items="items" item-key="name" :search="searchName" :filter="taskFilter" :custom-filter="customFilter" hide-actions>
      <template slot="items" slot-scope="props">
        <tr @click="openMark(props.item.id)">
          <td>{{ props.item.id }}</td>
          <td><v-chip small outline color="primary">{{getMethodName(props.item.method)}}</v-chip>{{ props.item.name }}</td>
          <td>{{ props.item.endTime }}</td>
          <td>{{ props.item.creditPerPic}}</td>
          <td>{{ props.item.creditFinished}}</td>
        </tr>
      </template>
      <template slot="no-data">
        <v-alert :value="true" color="error" icon="warning">
          抱歉，目前没有可用的任务 :(
        </v-alert>
      </template>
    </v-data-table>
    <v-container v-if="showType === 1" fluid>
      <v-data-iterator :items="items" content-tag="v-layout" :search="searchName" :filter="taskFilter" :custom-filter="customFilter" row wrap hide-actions>
        <v-flex slot="item" slot-scope="props" xs3>
          <v-card hover class="crad" :to="'/worker/mark/'+props.item.id">
            <v-card-media :src="'/api/pic/' + props.item.id + '/1'" height="140px">
              <v-container fill-height class="picture">
                <v-layout align-center>
                  <v-flex >
                    <span class="headline white--text">{{props.item.name}}</span>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-media>
              <v-card-text class="grey--text pl-0 pr-0 pt-0 pb-0">
                <v-container fluid>
                  <v-layout row><v-flex xs12>结束时间：&nbsp;{{props.item.endTime}}</v-flex></v-layout>
                  <v-layout row><v-flex xs12>每题奖励：&nbsp;{{props.item.creditPerPic}}</v-flex></v-layout>
                  <v-layout row><v-flex xs12>完成奖励：&nbsp;{{props.item.creditFinished}}</v-flex></v-layout>
                </v-container>
              </v-card-text>
          </v-card>
        </v-flex>
        <template slot="no-data">
          <v-alert :value="true" color="error" icon="warning">
            抱歉，目前没有可用的任务 :(
          </v-alert>
        </template>
      </v-data-iterator>
    </v-container>
  </div>
</template>

<script>
  export default {
    name: "TaskPanel",
    props: ['showType', 'type', 'searchName'],
    data() {
      return {
        headers: [
          {text: '编号', sortable: true, value: 'id'},
          {text: '任务名', sortable: false, value: 'name'},
          {text: '结束时间', sortable: true, value: 'endTime'},
          {text: '单张奖励', sortable: false, value: 'creditPerPic'},
          {text: '完成奖励', sortable: false, value: 'creditFinished'},
        ],
        items: []
      }
    },
    methods: {
      getMethodName(method) {
        switch (method) {
          case 1: return '单框标注';
          case 2: return '多框标注';
          case 3: return '不规则标注';
        }
      },
      taskFilter(val, search) {
        return val.indexOf(search) !== -1;
      },
      customFilter(items, search, filter) {
        let results = [];
        for (let i = 0; i < items.length; i++) {
          if (filter(this.items[i].name, search)) results.push(this.items[i]);
        }
        return results;
      },
      updateTasks() {
        this.$http.get('/task?type=' + this.type + '&workerId=' + sessionStorage.getItem('id'))
          .then((response) => {
            this.items = response.data;
          })
      },
      openMark(id) {
        this.$router.push('/worker/mark/' + id);
      }
    },
    created() {
      this.updateTasks();
    }
  }
</script>

<style scoped>
.card{
  margin: 10px;
}
.picture{
  background-color: dimgray;
  opacity: 0.6;
}
</style>



